.search__heading {
  @include null-margins;
  border-bottom: 1px solid $border--light;
  margin: 0;
  padding-bottom: unit(2);
  text-align: center;

  @include respond-to('medium-large') {
    margin: 0 unit(2);
    margin-bottom: unit(3) !important;
  }
}

.search__heading__link {
  a {
    @include arrow-link('orange');
  }
}

.search__heading__form {
  height: unit(2);
  margin: 0 auto;
  max-width: 58rem;
  position: relative;

  input[type='text'],
  input[type='search'] {
    padding-right: unit(4.5);
  }
}

.search__heading__submit {
  @include reset-button;
  @include arrow-link('orange');
  background: transparent;
  border: none;
  border-radius: 0;
  bottom: unit(0.5);
  color: $color--rose-of-sharon;
  font-weight: $font-weight--bold;
  height: unit(1);
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 3;

  @include hover {
    text-decoration: none;
  }
}
